<template>
  <div style="margin-top:100px;min-height:500px;">
    <div v-if="this.$store.state.arr.length">
      <div style="overflow:hidden;width:1190px;min-height:600px;box-sizing:border-box;padding-right:30px;">
        <div style="float:left;">
          <ul class="list">
            <li>商品</li>
            <li>价格</li>
            <li>数量</li>
            <li>合计</li>
          </ul>
          <ul v-for="i in this.$store.state.arr" class="poo">
            <li style="width:60px;height:100px;position: relative;">
              <span
                style="width:18px;height:18px;display: inline-block;border-radius: 50%;
    border: solid 2px #ddd;line-height:16px;text-align:center;position: absolute;top:45px;"
                @click="yc(i)"
              >×</span>
            </li>
            <li style="padding:10px;box-sizing:border-box;">
              <img style="width:90px;height:90px;" :src="i.img_url" alt />
            </li>
            <li
              style="text-overflow: ellipsis;white-space: nowrap;width:200px;line-height:100px;padding:0 10px;box-sizing:border-box;color:#519f10;overflow:hidden;"
            >{{i.name}}</li>
            <li
              style="width:88px;line-height:100px;padding:0 10px;box-sizing:border-box;color:#519f10;"
            >￥{{i.price}}</li>
            <li style="width:150px;line-height:100px;text-align:left;">
              <span
                style="text-align:center;display: inline-block;line-height:40px;border:solid 1px #ccc;border-right:none;width:26px;"
                @click="jj21(i)"
              >-</span>
              <span
                style="text-align:center;display: inline-block;line-height:40px;border:solid 1px #ccc;width:26px;margin: 0 -5px;"
              >{{i.n}}</span>
              <span
                style="text-align:center;display: inline-block;line-height:40px;border:solid 1px #ccc;border-left:none;width:26px;"
                @click="jj1(i)"
              >+</span>
            </li>
            <li
              style="line-height:100px;width: 90px;text-align: right;padding: 0 12px;box-sizing: border-box;"
            >￥{{i.price*i.n}}</li>
          </ul>
        </div>
        <div
          v-if="this.$store.state.arr"
          style="text-align:left;float:left;padding-left:30px;box-sizing:border-box;width:396px;"
        >
          <h3 style="border-bottom:solid 2px #ddd;font-size:16px;padding:6px 0;">购物车总计</h3>
          <div>
            <p class="ii">
              <span>购物车小计</span>
              <span>{{z_num}}</span>
            </p>
            <p class="ii2">
              <span>运费</span>
              <span>免费送货</span>
            </p>
            <p class="ii2" style="border-width:2px;margin-bottom:10px;">
              <span>订单总计</span>
              <span>{{}}</span>
            </p>
            <div style="width:100%;height:42px;">
              <input
                type="submit"
                value="前往收银台"
                name="proceed"
                style="color:#fff;background:#519f10;width:100%;height:42px;line-height:42px;font-family: BitCommerce;speak: none;font-weight: 400;font-variant: normal;text-transform: none;line-height: 1;-webkit-font-smoothing: antialiased;font-size: .75em;"
              />
            </div>
            <div>
              <h3
                style="font-size:16px;line-height:32px;height:32px;border-bottom:solid 3px #ddd;"
              >优惠券:</h3>
              <input
                type="text"
                placeholder="优惠劵代码"
                style="width:100%;height:37px;line-height:37px;font-size:18px;padding-left:10px;color:#777;border:solid 1px #ccc;box-sizing:border-box;margin-top:20px;"
              />
              <div style="width:100%;height:42px;">
                <input
                  type="submit"
                  value="使用优惠劵"
                  name="proceed"
                  style="color:#fff;margin-top:20px;background:#90af6b;width:100%;height:42px;line-height:42px;font-family: BitCommerce;speak: none;font-weight: 400;font-variant: normal;text-transform: none;line-height: 1;-webkit-font-smoothing: antialiased;font-size: .75em;"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div></div>
    </div>
    <div v-else>
      您的购物车目前是空的。
      <a
        style="display: inline-block;background:#519f10;color:#fff;line-height:1em;padding: 6px 10px;"
        href="#/sc"
      >去往商城</a>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  computed: {
    ...mapGetters(["z_num"])
  },
  methods: {
    ...mapActions(["aa", "jj", "jj2", "yc", "jj1", "jj21"])
  }
};
</script>
<style>
input {
  border: none;
  background: none;
  margin: 0;
}
.list {
  overflow: hidden;
  width: 760px;
  padding: 0 12px;
  box-sizing: border-box;
}
.list li {
  text-align: left;
  float: left;
}
.list li:nth-of-type(1) {
  width: 50%;
}
.list li:nth-of-type(2) {
  width: 88px;
  padding: 0 12px;
  box-sizing: border-box;
}
.list li:nth-of-type(3) {
  width: 150px;
  padding: 0 12px;
  box-sizing: border-box;
}
.list li:nth-of-type(4) {
  width: 90px;
  text-align: right;
  padding: 0 12px;
  box-sizing: border-box;
}
.poo {
  overflow: hidden;
  width: 760px;
  border-right: solid 1px #ccc;
}
.poo li {
  float: left;
}
.ii {
  overflow: hidden;
  padding-top: 10px;
  color: #777;
}
.ii2 {
  border-bottom: solid 1px rgba(221, 221, 221, 1);
  overflow: hidden;
  padding-top: 10px;
  color: #777;
}
.ii2 span {
  display: inline-block;
}
.ii2 span:nth-of-type(1) {
  float: left;
}
.ii2 span:nth-of-type(2) {
  float: right;
}
.ii span {
  display: inline-block;
}
.ii span:nth-of-type(1) {
  float: left;
}
.ii span:nth-of-type(2) {
  float: right;
}
</style>


